@import "vars";


/*==========  Desktop First  ==========*/

// ≥1200px
@include media-breakpoint-down(lg) {
    .weather {
        img {
            height: 68px; }
        h2 {
            font-size: 20px; }
        p {
            font-size: 10px; } }
    .currency {
        font-size: 18px; }
    .search {
        button {
            font-size: 18px; } }
    .field {
        font-size: 18px; }
    .team, .contacts {
        li {
            font-size: 14px;
            span {
                font-size: 16px; } } }
    .sliders {
	    padding: 0 10px; }
    .slider {
        width: 80%; } }

// ≥992px
@include media-breakpoint-down(md) {
    .search {
        button {
            width: 15%;
            font-size: 18px; } }
    .field {
        width: 83%;
        font-size: 18px; }
    .slider {
        width: 40%; }
    h2 {
        font-size: 20px; }
    p {
        font-size: 14px; }
    h3 {
        font-size: 18px; }
    .weather {
        margin-top: 30px; }
    .slider {
        width: 95%; }
    .mob-link {
        margin-bottom: 10px; }
    .event {
        .box {
            .comments {
                padding: 0; } } }
    .sliders {
        p {
            padding-top: 5px; } } }


// ≥768px
@include media-breakpoint-down(sm) {
    .side {
        margin-top: 30px; }
    .content {
	    margin-top: 80px; }
    header {
        position: fixed; }
    a {
        font-size: 12px; }
    .head-line {
	    height: 35px; }
    .line {
        height: 3px; }
    nav {
        height: 60px;
        padding: 0px;
        .logo {
            h1 {
                font-size: 20px;
                padding: 15px; } }
        button {
            background: none;
            float: right;
            color: $text2;
            font-size: 25px;
            padding: 12px 15px; } }
    .fixed {
        opacity: 0.9;
        position: fixed;
        top: 0;
        .head-line {
            transition: all 0s; }
        nav {
            height: 60px;
            .logo {
                h1 {
                    font-size: 20px;
                    padding: 15px; } } } }
    .weather {
        img {
            height: 75px; }
        h2 {
            font-size: 24px; }
        p {
            font-size: 12px; }
        margin-top: 0px; }
    .map {
        padding-top: 30px;
        margin-top: 60px;
        p {
            width: 100%; } }
    .location-main {}
    h1 {
        font-size: 18px; }
    footer {
        .line {
            p {
                font-size: 12px;
                padding: 18px 0; } } }
    .event {
        .rating {
	        margin-top: 15px; } }
    .about {
        margin-bottom: 15px; }
    .news {
        margin-top: 15px; }
    .block {
        margin-bottom: 15px; }
    footer {
        margin-top: 15px; }
    .currency {
        .fa-euro-sign {
            margin-top: 30px; } } }

// ≥576px
@include media-breakpoint-down(xs) {
    .search {
        button {
            width: 25%;
            font-size: 16px; } }
    .field {
        width: 72%;
        font-size: 16px; }
    footer {
        .line {
            p {
                font-size: 9px;
                padding: 22px 0; } } }
    .sliders {
        padding: 0; }
    .slider {
        width: 95%;
        p {
            top: -14px;
            font-size: 9px; } }
    .currency {
        .fa-euro-sign {
            margin-top: 0px; } } }




/*==========  Mobile First  ==========*/

// ≤576px
@include media-breakpoint-up(sm);

// ≤768px
@include media-breakpoint-up(md);

// ≤992px
@include media-breakpoint-up(lg);

// ≤1200px
@include media-breakpoint-up(xl);
