@import "vars";
@import "fonts";
@import "libs";

::placeholder {
	color: #666; }

::selection {
	background-color: $accent;
	color: #fff; }
html, body {
	margin: 0;
	padding: 0; }
body {
	background: #e9ebf2; }
*:focus, button:focus {
    outline: none; }
a {
	color: $text;
	text-decoration: none;
	&:hover {
		color: $text;
		text-decoration: none; } }
section {
	margin-top: 30px;
	transition: all 0.7s ease; }
header {
	position: absolute;
	top: 0;
	z-index: 50;
	background: #fff;
	width: 100%; }
.fixed {
	opacity: 0.9;
	position: fixed;
	top: -35px;
	.head-line {
		transition: all 0s; }
	nav {
		transition: all 0.5s ease;
		height: 70px;
		.logo {
			h1 {
				transition: all 0.5s ease;
				font-size: 25px;
				padding: 7px 0; } }
		ul {
			transition: all .5s ease;
			margin-top: 13px;
			li {
				a {
					font-size: 14px;
					padding: 26px 20px 25px 20px; } } } } }


.head-line {
	height: 35px;
	box-shadow: 0 0 5px rgba(46,72,80,0.65);
	padding: 5px 0;
	p {
		float: left;
		margin-right: 30px; }
	a {
		color: $text2;
		&:hover {
			color: $hover; } } }
.line {
	background: $accent;
	width: 100%;
	height: 3px; }
nav {
	transition: all .5s ease;
	width: 100%;
	height: 90px;
	color: $text2;
	padding: 10px 0;
	.logo {
		float: left;
		h1 {
			color: $text2;
			transition: all .5s ease;
			font-size: 30px;
			padding: 12px 0;
			margin: 0px; } }
	ul {
		transition: all .5s ease;
		float: right;
		margin-top: 25px;
		padding: 0px;
		li {
			float: left;
			display: inline;
			list-style: none;
			padding: 0px;
			margin: 0px;
			a {
				position: relative;
				color: $text2;
				font-weight: bold;
				padding: 36px 30px 35px 30px;
				transition: all .5s ease, background .2s ease-out;
				&:hover {
					background: $accent; } }
			.active {
				&:after {
					content: "";
					position: absolute;
					left: 50%;
					margin-left: -10px;
					bottom: 0;
					border: 10px solid transparent;
					border-bottom: 10px solid $accent; } } } } }

.content {
	margin-top: 150px;
	padding: 0 15px; }
.about {
	color: $text2;
	margin-bottom: 30px;
	overflow: hidden;
	width: 100%;
	background: #fff;
	.img {
		width: 100%;
		height: 270px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		&:after {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			background: $accent;
			opacity: 0.4; } }
	a {

		color: $hover;
		border-bottom: 1px $hover dashed;
		&:hover {
			color: $accent; } }
	.category {
		margin-bottom: 5px; }
	.info {
		padding: 10px; }
	p {
		margin-bottom: 10px; } }

.search {
	button {
		float: right;
		background: #fff;
		width: 10%;
		border: 1px solid #ccc;
		font-size: 20px;
		padding: 7px 10px; } }

.field {
	float: left;
	background: #fff;
	width: 89%;
	border: 1px solid #ccc;
	font-size: 20px;
	padding: 7px 10px; }

.news {
	overflow: hidden;
	width: 100%;
	margin-top: 30px;
	border-radius: 3px;
	background: #fff;
	transition: all 0.3s ease-out;
	.img {
		overflow: hidden;
		position: relative;
		width: 100%;
		height: 180px;
		background-position: center;
		background-size: cover;
		h3 {
			padding: 10px;
			text-align: center;
			bottom: 0;
			width: 100%;
			margin: 0;
			transition: all 0.3s ease-out; }
		a {
			position: relative;
			height: 40px;
			display: block;
			font-size: 20px;
			padding: 5px;
			bottom: -30%;
			border: 1px $text solid;
			border-radius: 25% / 100%;
			left: 19.65%;
			transition: bottom 0.2s ease-out 0.1s, background 0.25s ease-out 0s;
			span {
				position: relative;
				top: -3px;
				padding: 0 10px; }
			i {
				font-size: 28px; }
			&:hover {
				color: $text2;
				background: #fff; } }
		h3, a {
			color: $text;
			position: absolute;
			z-index: 3; }

		&:after {
			content: "";
			position: absolute;
			z-index: 1;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,0.4);
			transition: background 0.25s ease-out; } }
	.desc {
		overflow: hidden;
		height: 150px;
		background: #fff;
		padding: 15px;
		color: $text2;
		margin: 0; }
	.line {
		width: 90%;
		border-top: 1px #ccc solid;
		color: $text2;
		background: none;
		padding: 10px 0;
		margin: 0 5%;
		.category {
			float: left; }
		.date {
			float: right; } }
	p {
		font-size: 15px; }
	&:hover {
		transform: scale(1.03);
		cursor: pointer;
		.img {
			h3 {
				bottom: 45%; }
			a {
				bottom: 15%; }
			&:after {
				background: rgba(46,72,80,0.85); } } } }
button {
	position: relative;
	padding: 15px 0px;
	text-align: center;
	border: none;
	border-radius: 5px;
	cursor: pointer; }

.block {
	width: 100%;
	background: $accent2;
	margin-bottom: 30px;
	h3 {
		margin: 0;
		background: $accent;
		color: $text;
		text-align: center;
		width: 100%;
		padding: 10px; } }


