.weather {
	position: relative;
	background: $accent;
	padding: 15px;
	padding-right: 30px;
	.info {
		width: 53%;
		float: right; }
	img {
		height: 90px; }
	h2 {
		font-size: 26px;
		span {
			font-weight: normal; }
		#temp {
			font-weight: bold; } }
	p, h2 {
		text-align: right;
		width: 100%;
		margin: 0;
		float: right;
		color: $text; }
	p {
		font-size: 13px; }
	.loader {
		padding: 20px;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $accent;
		z-index: 2;
		transition: all 0.5s ease; }
	.show {
		opacity: 0; } }
.line-weather {
	height: 35px;
	background: #20343a;
	button {
		padding: 0;
		font-size: 12px;
		float: right;
		background: none;
		color: $text;
		&:hover {
			color: $hover; }
		i {
			font-size: 18px;
			padding: 8px 10px 5px 5px; } }
	p {
		float: left;
		color: $text;
		padding: 5px 10px; } }


.currency {
	background: #fff;
	padding: 10px 0;
	padding-right: 10px;
	font-size: 24px;
	font-weight: bold;
	.left {
		text-align: right;
		width: 100%; }
	.right {
		text-align: right;
		width: 100%; }
	.green {
		color: #26bc26; }
	.red {
		color: #bc2626; }
	.type {
		color: $accent;
		font-size: 12px;
		font-weight: normal;
		margin-bottom: 5px;
		text-align: center;
		width: 100%; }
	.fa-dollar-sign,.fa-euro-sign {
		text-align: right;
		width: 100%; }
	.fa-dollar-sign {
		margin-top: 30px; } }
.top {
	padding: 0 15px;
	background: #fff;
	.sidebar {
		ul {
			list-style: none;
			padding: 0;
			margin: 0;
			li {
				padding: 15px 0;
				word-wrap: break-word;
				border-top: 1px #ccc solid;
				&:first-child {
					border: none; } } }
		a {
			color: $accent;
			&:hover {
				color: $hover; } }
		p {
			margin: 0; }
		span {
			color: #aaa;
			font-size: 10px; } } }
footer {
	margin-top: 30px;
	background: $accent;
	color: $text;
	transition: all 0.7s ease;
	.copy {
		padding: 10px 0;
		float: right; }
	.team {
		margin-right: 5%;
		width: 100%;
		float: left;
		padding: 0;
		li {
			position: relative;
			margin-bottom: 10px;
			font-size: 18px;
			list-style: none;
			span {
				position: relative;
				opacity: 1;
				transform: scaleX(1);
				transition: all .5s ease;
				left: 0; }
			i {
				position: relative;
				margin-right: 10px;
				padding: 12px 0;
				text-align: center;
				width: 42px;
				height: 42px;
				background: $accentHover;
				font-size: 20px;
				border-radius: 100%;
				transition: all .5s ease;
				z-index: 2; }
			p {
				background: $accentHover;
				padding: 5px 25px 5px 50px;
				border-radius: 0 10px 10px 0;
				position: absolute;
				top: 5px;
				left: -60px;
				z-index: 0;
				transform: scaleX(0);
				transition: all .5s ease; }
			&:hover {
				cursor: default;
				i {
					transform: rotateZ(360deg) scale(1.1);
					background: $hover; }
				span {
					left: -50px;
					transform: scaleX(0); }
				p {
					left: 20px;
					transform: scaleX(1); } } } }
	h3 {
		font-size: 22px;
		margin: 30px 0; }
	.fa-phone {
		transform: scale(-1, 1); }
	.contacts {
		margin: 0;
		padding: 0;
		list-style: none;
		li {
			font-size: 16px;
			height: 35px;
			float: left;
			i {
				padding: 0 10px;
				background: none;
				border-radius: 10%; } } }
	.team-2 {
		margin-top: 93px; }
	.line {
		width: 100%;
		height: auto;
		background: rgba(0,0,0, 0.3);
		h1 {
			margin: 0;
			padding: 15px 0; }
		p {
			padding: 26px 0;
			margin: 0; } }
	.links {
		margin-top: 80px;
		float: right;
		a {
			padding-left: 40px;
			transition: all 0.2s ease-out;
			&:hover {
				color: $hover; } }
		i {
			width: 100%;
			text-align: center;
			font-size: 40px; } } }

.menu {
	padding-top: 63px;
	overflow:  auto;
	position: fixed;
	top: 0;
	right: -100%;
	opacity: 1;
	width: 75%;
	height: 100vh;
	z-index: 10;
	background: #fff;
	transition: all .7s ease;
	ul {
		margin: 0;
		list-style: none;
		padding: 0;
		li {
			width: 100%;
			padding: 0;
			border-bottom: 1px #ccc solid;
			a {
				display: block;
				width: 100%;
				padding: 15px 20px;
				font-size: 16px;
				color: $accent; }
			&:hover {
				background: $hover;
				a {
					color: $text; } }
			&:first-child {
				border-top: 1px #ccc solid; } } }
	.loc {
		background: #f4f4f4;
		display: block;
		width: 100%;
		padding: 10px 20px;
		font-size: 12px;
		color: $accent; }
	.login-menu {
		background: none;
		height: 48px;
		.sign-button, .login-button {
			color: $hover;
			display: block;
			padding:  12px 20px;
			font-size: 15px;
			&:hover {
				color: $accentHover; } }
		.sign-button {
			float: right; }
		.login-button {
			float: left; } }
	.active {
		background: $accent;
		a {
			color: $text; } } }
.menu-active {
	right: 0%;
	box-shadow: 0 0 5px rgba(46,72,80,0.65); }

.body-fixed {
	transform: translateX(-75%); }
.map {
	position: relative;
	overflow: hidden;
	margin-top: 125px;
	background: url("../img/map.jpg");
	background-size: contain;
	width: 100%;
	height: 170px;
	padding-top: 40px;
	&:after {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		background: rgba(46,72,80,0.7); }
	h1, p {
		position: relative;
		z-index: 2;
		color: $text; }
	p {
		width: 50%; } }
.location-main {
	#cities {
		padding: 5px 10px;
		width: 100%; }
	#selectCity {
		display: none; }
	ul {
		display: none;
		padding: 0;
		list-style: none;
		position: absolute;
		z-index: 10;
		width: 100%;
		background: #fff;
		border: 1px #ccc solid;
		li {
			padding: 5px 10px;
			&:hover {
				cursor: pointer;
				background: $hover;
				color: $text; }
			p {
				font-size: 16px;
				margin: 0;
				font-weight: bold; }
			span {
				font-size: 14px; }
			.cityVal {
				display: none; } } }
	.focus {
		display: block; }
	label {
		padding-top: 7px;
		margin-right: 20px; }
	.checkBox {
		margin-top: 30px;
		input {
			transform: scale(1.5);
			margin-left: 3px;
			margin-right: 10px;
			&:hover {
				cursor: pointer; } }
		label {
			&:hover {
				cursor: pointer; } }
		p {
			width: 50%;
			color: #999;
			font-size: 14px; } }
	button {
		margin-top: 30px;
		padding: 10px 20px;
		border: 1px $accent solid;
		background: #fff;
		&:hover {
			background: #eee; } }
	.save {
		background: $accent;
		color: $text;
		&:hover {
			background: $accentHover; } } }
.white {
	background: #fff; }

.login {
	background-image: url("../img/login_bg.jpg");
	margin: 0;
	&:after {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
 }		//background: rgba(46,72,80,0.9)
	.row {
		min-height: 100vh; }
	.box {
		position: relative;
		z-index: 1;
		padding: 50px 55px;
		width: 100%;
		height: auto;
		background: #fff;
		box-shadow: 0 0 50px rgba(46,72,80,0.3);
		.loginLabel {
			position: relative;
			input {
				padding-bottom: 10px;
				color: $text2;
				background: none;
				border: none;
				border-bottom: 1px solid $text2;
				font-size: 16px;
				width: 100%;
				margin-bottom: 45px; }
			.info-label {
				color: $text2;
				font-size: 16px;
				transition: 0.5s; }
			.error-label, .info-label, i {
				position: absolute;
				pointer-events: none; }
			.error-label {
				font-size: 12px;
				display: none;
				bottom: 15px;
				color: #F00; }
			i {
				padding: 10px;
				right: 0px;
				color: #090;
				display: none; } }
		.focus {
			.info-label {
				transition: 0.5s;
				transform: translateY(-20px);
				font-size: 12px; } }
		.error {
			.error-label {
				display: block; }
			.info-label {
				color: #f00; }
			input {
				border-color: #f00;
				border-width: 2px; } }
		.success {
			.info-label {
				color: #090; }
			input {
				border-color: #090;
				border-width: 2px; }
			i {
				display: block; } }
		button {
			font-size: 16px;
			color: $text;
			width: 100%;
			background: $accent;
			&:hover {
				background: $accentHover; } }
		p {
			margin: 0;
			margin-top: 30px; }
		a {
			font-weight: bold;
			color: $hover;
			border-bottom: 2px $hover dashed;
			&:hover {
				color: $text2;
				border-bottom: 2px $text2 dashed; }
			h1 {
				color: $accent;
				width: 100%;
				text-align: center;
				font-size: 30px;
				margin-bottom: 40px; } } } }
.blue {
	color: $hover; }
.login-buttons {
	background: none;
