			color: $accent;
			&:hover {
				color: $hover; } }
		.img {
			margin: 0;
			margin-right: 15px;
			width: 40px;
			height: 40px;
			float: left; }
		.rating-list {
			.slider {
				float: left; }
			i {
				text-align: center;
				height: 24px;
				width: 24px;
				float: left;
				font-size: 18px;
				color: $accent; }
			li {
				min-height: 82px; }

			p {
				margin-bottom: 5px; }
			.voice-button, .comment-button {
				width: 100%;
				height: 100%;
				text-align: center;
				padding: 0;
				background: none;
				span {
					color: $accent;
					font-size: 10px; }
				i {
					font-size: 25px;
					text-align: center;
					width: 100%; }
				&:hover {
					i, span {
						color: $hover; } } } } }
	.sidebar {
		ul {
			list-style: none;
			padding: 0;
			margin: 0;
			li {
				padding: 15px 0;
				word-wrap: break-word;
				border-top: 1px #ccc solid;
				&:first-child {
					border: none; } } }
		a {
			color: $accent;
			&:hover {
				color: $hover; } }
		p {
			margin: 0; }
		span {
			color: #aaa;
			font-size: 10px; } }
	.add {
		label {
			margin: 10px 0;
			width: 100%; }
		input, textarea, select {
			width: 100%;
			border: 1px #aaa solid;
			font-size: 16px;
			padding: 10px; }
		textarea {
			min-height: 150px; }
		button {
			width: 100%;
			margin-top: 40px;
			padding: 15px 40px;
			background: $accent;
			color: $text;
			&:hover {
				background: $accentHover; } }
		.file {
			border: none;
			width: 49%; } } }
.sliders {
	padding: 0 10px; }
.slider {
	margin: 0 10px;
	margin-top: 12px;
	margin-bottom: 7px;
	position: relative;
	width: 90%;
	height: 8px;
	background: $accent;
	.line-slider {
		z-index: 1;
		position: absolute;
		height: 100%;
		background: $hover;
		opacity: 0.7; }
	p {
		padding: 0 !important;
		position: absolute;
		top: -18px;
		font-size: 12px;
		color: #aaa;
		left: 0; } }
.voice {
	#true-slider, #bias-slider {
		margin-top: 7px;
		width: 100%;
		float: left; }
	input {
		width: 100%;
		border: none;
		float: left; }
	label {
		margin: 0; }
	textarea {
		width: 100%;
		min-height: 200px; }
	button {
		padding: 10px 20px;
		border: 1px $accent solid;
		background: #fff;
		&:hover {
			background: #eee; } }
	.save {
		background: $accent;
		color: $text;
		&:hover {
			background: $accentHover; } } }
.close {
	background: none;
	font-size: 25px;
	padding: 0; }
.ads {
	max-width: 100%;
	background: #fff;
	img {
		width: 100%; } }
.collapse {
	display: none; }
.load-more {
	font-size: 18px;
	background: $text;
	color: $accent;
	width: 100%;
	&:hover {
		cursor: pointer;
		color: $hover; } }
.media {
	margin-bottom: 15px; }
.add-media {
	display: none;
	float: right;
	padding: 10px 15px;
	margin-bottom: 15px;
	background: none;
	color: $accent;
	&:hover {
		color: $hover; } }
.add-media-modal {
	input, select {
		width: 100%;
		margin-bottom: 15px;
		padding: 10px; } }
.uAdmin {
	.add-media {
		display: block; } }

@import "media"; // Always at the end
